import React from "react";
import "./Covid.css";
import Card from "./Card";

const Covid = ({ data }) => {
  // console.log(data);
  return (
    <div className="Main">
      <div className="Toolbar">
        <h1>Covid 19</h1>
      </div>
      <div className="Content">
        {data.map((d) => {
          return (
            <Card
              accDefRate={d.accDefRate}
              accExamCnt={d.accExamCnt}
              accExamCompCnt={d.accExamCompCnt}
              careCnt={d.careCnt}
              clearCnt={d.clearCnt}
              createDt={d.createDt}
              deathCnt={d.deathCnt}
              decideCnt={d.decideCnt}
              examCnt={d.examCnt}
              key={d.seq}
              stateDt={d.stateDt}
              stateTime={d.stateTime}
              updateDt={d.updateDt}
            />
          );
        })}
      </div>
    </div>
  );
};

export default Covid;
