import React, { useState, useEffect, useReducer } from "react";
import CovidPresenter from "./Covid.presenter";
import PropTypes from "prop-types";
import TestData from "../Test.xml";

const Covid = () => {
  const [data, setData] = useState(null);
  const [result, setResult] = useState([]);

  useEffect(() => {
    // setData(TestData);

    fetch(TestData)
      .then((r) => r.text())
      .then((text) => {
        // console.log(text);
        setData(text);
      });
  }, []);

  useEffect(() => {
    const objs = [];
    const doc = new DOMParser().parseFromString(data, "text/xml");
    const items = doc.getElementsByTagName("item");

    for (let i = 0; i < items.length; i++) {
      objs.push({
        accDefRate: items[i].getElementsByTagName("accDefRate")[0].innerHTML,
        accExamCnt: items[i].getElementsByTagName("accExamCnt")[0].innerHTML,
        accExamCompCnt: items[i].getElementsByTagName("accExamCompCnt")[0]
          .innerHTML,
        careCnt: items[i].getElementsByTagName("careCnt")[0].innerHTML,
        clearCnt: items[i].getElementsByTagName("clearCnt")[0].innerHTML,
        createDt: items[i].getElementsByTagName("createDt")[0].innerHTML,
        deathCnt: items[i].getElementsByTagName("deathCnt")[0].innerHTML,
        decideCnt: items[i].getElementsByTagName("decideCnt")[0].innerHTML,
        examCnt: items[i].getElementsByTagName("examCnt")[0].innerHTML,
        seq: items[i].getElementsByTagName("seq")[0].innerHTML,
        stateDt: items[i].getElementsByTagName("stateDt")[0].innerHTML,
        stateTime: items[i].getElementsByTagName("stateTime")[0].innerHTML,
        updateDt: items[i].getElementsByTagName("updateDt")[0].innerHTML,
      });
    }

    setResult(objs);
  }, [data]);

  return <CovidPresenter data={result} />;
};

// Covid.propTypes = {
//   accDefRate: PropTypes.number.isRequired,
// };

export default Covid;
